import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import './styles.css';
import _ from 'lodash';
import {
  ADD_TO_CART, REMOVE_FROM_CART,
} from '../../../constants/actionTypes';
import ProductDetails from './ProductDetails';
import CartDetails from './CartDetails';
import CollapseTransition from '../../../common/Components/CollapseTransition';

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      user: {},
      product: { },
      quantity: 1,
      images: [],
      alreadyInCart: false,
      cartItem: false,
      isSizesAvailable: false,
      selectedSize:"",
      sizes:[],
    };
    this.cartItems = [];
  }

  componentWillMount() {
    if (this.props.data) {
      this.setState({ product: this.props.data });
      const { data } = this.props;
      if (data.meta_data && data.meta_data.sizes_available) {
        this.setState({ sizes: data.meta_data.sizes_available, isSizesAvailable: data.meta_data.sizes_available.length > 0 });
      }
    }
    if (this.props.cartItem) {
      this.setState({ cartItem: this.props.cartItem });
    }
    if (this.props.cartItems) {
      this.cartItems = this.props.cartItems;
      const temp = this.props.cartItems.filter((c) => c.item.id === this.props.data.id);
      if (temp.length > 0) {
        this.setState({ alreadyInCart: true, quantity: temp[0].quantity,selectedSize:temp[0].selectedSize });
      } else {
        this.setState({ alreadyInCart: false, quantity: 1, selectedSize: ""});
      }
    }
  }

  componentWillReceiveProps(np) {
    if (np.data && np.data !== this.state.product) {
      this.setState({ product: np.data });
      const { data } = np;
      if (data.meta_data && data.meta_data.sizes_available) {
        this.setState({ sizes: data.meta_data.sizes_available, isSizesAvailable: data.meta_data.sizes_available.length > 0 });
      }
    }
    if (np.cartItems) {
      this.cartItems = np.cartItems;
      const temp = np.cartItems.filter((c) => c.item.id === np.data.id);
      
      if (temp.length > 0) {
        this.setState({ alreadyInCart: true, quantity: temp[0].quantity,selectedSize:temp[0].selectedSize });
      } else {
        this.setState({ alreadyInCart: false, quantity: 1, selectedSize: ""});
      }
    }
  }

  modifyQuantity(change) {
    if (this.state.quantity + change > 0) this.setState({ quantity: this.state.quantity + change });
  }

  addToCart() {
    if (this.state.isSizesAvailable && this.state.selectedSize === '') {
      this.setState({ showSizes: true });
    } else {
      const data = {
        item: this.state.product,
        quantity: this.state.quantity,
        selectedSize: this.state.selectedSize
      };
      console.log(data)
      this.props.addToCart(data);
      this.setState({showSizes:false})
    }
  }

  removeProduct() {
    this.props.removeFromCart(this.state.product);
  }

  selectSize(e) {
    console.log(e.target.value);
    this.setState({ selectedSize: e.target.value });
  }

  render() {
    const {
      name, desc, disc_price, price, stock, estimated_delievry, images,
    } = this.state.product;
    const {
      quantity, alreadyInCart, cartItem, showSizes,
    } = this.state;
    return (
      <div className="public-product relative">

        <div className="product-header-img relative" style={{ overflow: 'hidden' }}>
          {
            images.length > 0
              ? <img src={images[0]} alt="" /> : <img src="" alt="" />
          }
          <ProductDetails data={this.state.product} alreadyInCart={alreadyInCart} parent={this.props.parent} />
        </div>
        <div className="product-pricing">
          <h3 className="ma0 relative">
            <ProductDetails data={this.state.product} alreadyInCart={alreadyInCart} parent={this.props.parent} />
            {name}
          </h3>
          {
            disc_price === price
              ? (
                <b>
                    Rs.
                  {price}
                </b>
              )
              : (
                <div>
                  <div className="home-page-product-sale">
                    {`${Math.floor(((price - disc_price) / price) * 100)}%off`}
                  </div>
                  <b>
                    Rs.
                    {disc_price}
                  </b>
                  <strike>{` Rs. ${price}`}</strike>

                </div>
              )
          }
          {
          cartItem
            ? (
              <div className="product-bottom-div">
                <div className="f6 go-to-cart relative blue" onClick={this.removeProduct.bind(this)}>
                  Remove
                </div>
              </div>
            )
            : (
              <div className="product-bottom-div">
                <div className="flex">
                  <button type="button" className="w-20" onClick={() => this.modifyQuantity(-1)}> - </button>
                  <input type="number" value={quantity} className="w-60" disabled />
                  <button type="button" className="w-20" onClick={() => this.modifyQuantity(1)}>  + </button>
                </div>
                <CollapseTransition visible={showSizes}>
                  <div className='flex flex-column mt1 mb1' >
                    <select onChange={this.selectSize.bind(this)} defaultValue={this.state.selectedSize} >
                      <option selected value={""}>Select Size</option>

                      {
                        this.state.sizes.map((s, i) => (
                          <option value={s} key={i}>{s}</option>
                        ))
                      }
                    </select>
                    {/* <button className="" type="button" onClick={() => this.setState({ showSizes: false })}> Cancel </button> */}
                  </div>
                </CollapseTransition>
                <button className="product-add-to-cart" onClick={this.addToCart.bind(this)} > Add to cart </button>
                {
                  alreadyInCart
                    ? (
                      <div className="f6 go-to-cart relative">
                          Item already in cart.
                        <a>
                        Go To Cart
                        </a>
                        <CartDetails />
                      </div>
                    )
                    : null
                }
              </div>
            )
        }
        </div>


      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  cartItems: state.HomePageReducers.cartItems,


});

const mapDispatchToProps = (dispatch) => ({
  addToCart: (product) => dispatch({ type: ADD_TO_CART, payload: product }),
  removeFromCart: (product) => dispatch({ type: REMOVE_FROM_CART, payload: product }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
