/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { push } from 'react-router-redux';
import { toast } from 'react-toastify';
import CloseIcon from '@material-ui/icons/Close';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import './HomePage.css';

import agent from '../../agent';
import ProfilePIc from '../../common/Components/ProfilePIc';
import Loader from '../../common/Components/Loader';
import { store } from '../../store';
import { GET_USER_DETAILS, GET_PUBLIC_PAGE_HEADER_INFO } from '../../constants/actionTypes';
import Logo from '../../common/Components/Logo';
import { DEFAULT_BG_STYLE, DEFAULT_TEXT_COLOR } from '../../constants/otherConstants';
import ChangeProfilePic from '../../common/Components/ChangeProfilePic';
import CollapseTransition from '../../common/Components/CollapseTransition';
import lightOrDark from '../../common/Components/checkBgColor';
import Product from './components/Product';
import CartDetails from './components/CartDetails';

class HomePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      img: null,
      loading: false,
      user: { profile_pic: null, link: [], username: '' },
      error: '',
      bgStyle: null,
      togelSignUpMessage: true,
      textColor: DEFAULT_TEXT_COLOR,
      products: [],
      render: false,
    };
    this.links = [];
    this.cartItems = [];
  }

  componentWillMount() {
    if (this.props.parent) {
      if (this.props.parent === 'admin-page') {
        if (this.props.links) {
          this.links = this.props.links.links;
        }
        if (this.props.user) {
          this.setState({ user: this.props.user.user, togelSignUpMessage: false });
        }
        if (this.props.homeBgStyle) {
          let data = JSON.parse(DEFAULT_BG_STYLE);
          let textColor = DEFAULT_TEXT_COLOR;
          if (this.props.homeBgStyle.background_color) {
            try {
              data = JSON.parse(this.props.homeBgStyle.background_color);
              textColor = lightOrDark(data.backgroundColor);
            } catch {
              data = JSON.parse(DEFAULT_BG_STYLE);
            }
          }
          if (this.props.homeBgStyle.link_style) {
            textColor = this.props.homeBgStyle.link_style;
          }
          if (this.props.homeBgStyle.background_image) {
            data = { ...data, backgroundImage: `url(${this.props.homeBgStyle.background_image})` };
          }
          this.setState({ bgStyle: data, togelSignUpMessage: false, textColor });
        }
        if (this.props.products && this.props.products.products) {
          this.setState({ products: this.props.products.products });
        }
      }
    } else {
      // cart Items related info
      if (this.props.cartItems) {
        this.cartItems = this.props.cartItems;
        this.setState({ render: !this.state.render });
      }
      // other api callls
      const { userName } = this.props.match.params;
      const data = { username: userName };
      agent.AdminPage.addViewToProfile(data);
      this.props.getPublicPageHeaderInfo(data);
      this.setState({ loading: true });
      agent.ProductsPage.getProducts(data).then((res) => {
        this.setState({ products: res.data.products });
      }).catch((err) => {
        console.log(err);
        if (err.response) {
          console.log(err.response);
          toast.error(err.response.error);
        }
      });
      agent.AdminPage.getBackground(data).then((res) => {
        let textColor = DEFAULT_TEXT_COLOR;
        let data = {};
        if (res.data) {
          if (res.data.background_color) {
            try {
              data = JSON.parse(res.data.background_color);
              textColor = lightOrDark(data.backgroundColor);
            } catch {
              data = JSON.parse(DEFAULT_BG_STYLE);
            }
          } else {
            data = JSON.parse(DEFAULT_BG_STYLE);
          }
          if (res.data.link_style) {
            textColor = res.data.link_style;
          }
          if (res.data.background_image) {
            data = { ...data, backgroundImage: `url(${res.data.background_image})` };
          }
          this.setState({ bgStyle: data, textColor });
        } else {
          this.setState({ bgStyle: JSON.parse(DEFAULT_BG_STYLE), textColor });
        }
      });
      this.setState({ loading: true });
      agent.Auth.getUserDetails(data).then((res) => {
        this.links = res.data.user.links;
        this.setState({ user: res.data.user, loading: false });
      }).catch((err) => {
        if (err.response && err.response.status === 500) {
          toast.error('user Not Found');
          this.setState({ loading: false });
          console.log(err.response);
          setTimeout(() => store.dispatch(push('/')));
        } else {
          toast.error('Error has occured');
          this.setState({ loading: false });
        }
      });
    }
  }

  componentWillReceiveProps(np) {
    if (this.props.parent === 'admin-page') {
      if (np.links && np.links.links !== this.links) {
        this.links = np.links.links;
        this.setState({ loading: false });
      }
      if (np.user && np.user.user !== this.state.user) {
        this.setState({ user: np.user.user });
      }
      if (np.homeBgStyle) {
        let data = {};
        let textColor = DEFAULT_TEXT_COLOR;
        if (np.homeBgStyle.background_color) {
          try {
            data = JSON.parse(np.homeBgStyle.background_color);
            textColor = lightOrDark(data.backgroundColor);
          } catch {}
        }

        if (np.homeBgStyle.link_style) {
          textColor = np.homeBgStyle.link_style;
        }

        if (np.homeBgStyle.background_image) {
          data = { ...data, backgroundImage: `url(${np.homeBgStyle.background_image})` };
        }
        this.setState({ bgStyle: data, textColor });
      }
      if (np.products && np.products.products) {
        this.setState({ products: np.products.products });
      }
      this.setState({ loading: false });
    } else if (np.cartItems && np.cartItems !== this.cartItems) {
      this.cartItems = np.cartItems;
      this.setState({ render: !this.state.render });
    }
  }

  addClickToLink(link) {
    if (this.props.parent !== 'admin-page') {
      agent.AdminPage.addClickToLink({ link_id: link.id });
    }
  }

  render() {
    const { username, profile_pic } = this.state.user;
    const { textColor } = this.state;
    return (
      <div className="gradientbg home-page-bg mh-100" style={this.state.bgStyle ? { ...this.state.bgStyle } : null}>
        <div className="  ">
          <div className="dyna-width">
            <div className=" center">
              {
                this.state.products.length > 0
                  ? (
                    <button className="home-page-cart-div">
                      <ShoppingCartIcon style={{ color: textColor }} />
                      <CartDetails />
                      <span style={{ color: textColor }}>
                  Cart(
                        {this.cartItems.length}
                  )
                      </span>
                    </button>
                  )
                  : null
              }

              <article className="center  tc ">
                {
                  this.props.parent === 'admin-page' ? <ChangeProfilePic /> : <ProfilePIc photo={profile_pic} changeProfile={false} />
                }
                <h1 className="subheading white center tc" style={{ color: textColor }}>
                  {`@${username}`}
                </h1>
                <p className="err-txt">{`${this.state.error}`}</p>
              </article>
            </div>
            <div className="tc mt4">
              {
                this.links.map((link, i) => {
                  if (link.visible) {
                    return (
                      <a href={link.url} onClick={() => this.addClickToLink(link)} key={i} className="link " target="_blank" rel="noopener noreferrer" style={{ color: textColor, border: `2px solid ${textColor}` }}>
                        {link.icon ? <img src={link.icon} alt="" /> : null}
                        <p className="center ">
                          {`${link.title}`}
                        </p>
                      </a>
                    );
                  }
                  return null;
                })
              }

            </div>
          </div>
          {
            this.state.products.length > 0
              ? (
                <div className="home-page-products-div" style={{ justifyContent: 'space-evenly' }}>
                  <div className="home-page-products-top-div">
                    <h3>Our Products</h3>
                  </div>
                  <div className="home-page-products-content" style={{ justifyContent: 'space-evenly' }}>
                    {
                this.state.products.map((p, i) => (
                  <Product data={p} key={p.id} parent={!this.props.parent ? 'public-home-page' : this.props.parent} />
                ))
              }
                  </div>
                </div>
              )
              : null
          }

        </div>

        <footer className="">

          <Logo
            logostyles={{
              display: 'flex', flexDirection: 'row', margin: 'auto', color: this.state.textColor,
            }}
            parent={!this.props.parent ? 'public-home-page' : this.props.parent}
            username={username}
          />

          <CollapseTransition visible={this.state.togelSignUpMessage}>
            <div className="signup-message">
              <p>
                Want a similar page of your own?
                <a href="/login">Click here to signup</a>
              </p>
              <button className="" type="button" onClick={() => this.setState({ togelSignUpMessage: !this.state.togelSignUpMessage })}><CloseIcon /></button>
            </div>
          </CollapseTransition>
        </footer>
        {
              this.state.loading
                ? <Loader />
                : null
          }
      </div>
    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  links: state.AdminPageReducers.links,
  homeBgStyle: state.AdminPageReducers.homeBgStyle,
  products: state.ProductsPageReducers.products,
  cartItems: state.HomePageReducers.cartItems,
});

const mapDispatchToProps = (dispatch) => ({
  getUserDetails: (user) => store.dispatch({ type: GET_USER_DETAILS, payload: agent.Auth.getUserDetails(user) }),
  getPublicPageHeaderInfo: (user) => dispatch({ type: GET_PUBLIC_PAGE_HEADER_INFO, payload: agent.AdminPage.getHeaderInfo(user) }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(HomePage);
